import React, {useState} from "react";


var player: string = "x";


let App: React.FC= () =>
{                                   //0    1     2     3     4     5      6     7    8
    var [face, setFace] = useState(["..", "..", "..",  "..", "..", "..", "..", "..", "..",]);
    var [isMov, setIsMove] = useState([true,true,true,true,true,true,true,true,true,]);
    var [winner, setWinner] = useState("Ongoing play");
    // function to check game end !
    var Victory = () =>{
        let f: string[] = face;
        // horizontal:
        if(f[0]!==".." && f[0]===f[1] && f[1]===f[2]) return f[0];
        if(f[3]!==".." && f[3]===f[4] && f[4]===f[5]) return f[3];
        if(f[6]!==".." && f[6]===f[7] && f[7]===f[8]) return f[6];
        // vertical:
        if(f[0]!==".." && f[0]===f[3] && f[3]===f[6]) return f[0];
        if(f[1]!==".." && f[1]===f[4] && f[4]===f[7]) return f[1];
        if(f[2]!==".." && f[2]===f[5] && f[5]===f[8]) return f[2];
        // diagonals:
        if(f[0]!==".." && f[0]===f[4] && f[4]===f[8]) return f[0];
        if(f[2]!==".." && f[2]===f[4] && f[4]===f[6]) return f[2];
    }

    var modFace = (i: number) =>{
        //
        if(Victory()==="x") setWinner("player x wins!");
        if(Victory()==="o") setWinner("player o wins!");
        //
        if(isMov[i]===true){
        var newArray: string[] = [];
        newArray = newArray.concat(face.slice(0,i), [player], face.slice(i+1,9));
        //face = newArray;
        if(player==="x")player = "o"; else player = "x";
        isMov[i] = false;
        return newArray;
        }
        else return face;
    
    }

    return(
    <div>
      <div>
        <button onClick={()=>setFace(modFace(0))}>{face[0]}</button>
        <button onClick={()=>setFace(modFace(1))}>{face[1]}</button>
        <button onClick={()=>setFace(modFace(2))}>{face[2]}</button>
      </div>
      <div>
        <button onClick={()=>setFace(modFace(3))}>{face[3]}</button>
        <button onClick={()=>setFace(modFace(4))}>{face[4]}</button>
        <button onClick={()=>setFace(modFace(5))}>{face[5]}</button>
      </div>
      <div>
        <button onClick={()=>setFace(modFace(6))}>{face[6]}</button>
        <button onClick={()=>setFace(modFace(7))}>{face[7]}</button>
        <button onClick={()=>setFace(modFace(8))}>{face[8]}</button>
      </div>
        <button>{winner}</button>

    </div>
    );
}

export default App;